class CardRoom {
  #className = "card-room";

  #$component;
  #options;

  constructor($parent, options) {
    this.#init($parent, options);
  }

  #init($parent, options) {
    this.#options = options;

    this.#$component = $parent.find(`.js-${this.#className}`);
  }
}

export default CardRoom;